import React,{useEffect,useState} from 'react'
import '../css/Details.css'
import { Link,useLocation } from 'react-router-dom'
import axios from 'axios'

export default function Details() {

    // assign the url for getting data from the api
    const location = useLocation()
    console.log(location.search);
    const url = "https://shijus-studentapi.herokuapp.com/individual"+location.search
    console.log(url);
    const [user,setUser]=useState([])

    // useEffect is used to get data before the page gets renderd
    useEffect(()=>{
        getsingle()
    },[])

    // Used axios to fetch data from api
    const getsingle = ()=>{
        axios.get(url)
        .then((res)=>{
            setUser(res.data)
        })
        .catch((err)=>console.log("Error"))
    }
    return (
        <div className="mainitm">
            <h2>ABC School Newyork</h2>
            <p className="h5 items">Rollno: {user.rollno}</p>
            <p className="h5 items">Name: {user.name}</p>
            <p className="h5 items">Dob: {user.dob}</p>
            <p className="h5 items">Class: {user.cls}</p>
            <p className="h5 items">Division: {user.division}</p>
            <p className="h5 items">Gender: {user.gender}</p>
            <Link to='/'>
                <button className="btn btn-primary" type="submit" style={{marginLeft:'70%'}}>New</button>
            </Link>
        </div>
    )
}
