import React from 'react'
import './css/App.css'
import RegPage from './components/page/reg-page';
import DetailPage from './components/page/detail-page';
import {HashRouter as Router,Route} from 'react-router-dom'
function App() {
  return (
    <div className="App">
      <Router>
        <Route path='/' exact component={RegPage} />
        <Route path='/details' component={DetailPage} />
      </Router>
    </div>
  );
}

export default App;
