import React,{useEffect,useState}  from 'react'
import axios from 'axios'

export default function Tablerow() {
    // url for the api to get all details of student
    const url= "https://shijus-studentapi.herokuapp.com/details"
    const [data,setdata]=useState([]);

    //for getting data before page renderd
    useEffect(()=>{
        getData();
    },[])

    //fetching data from api
    const getData=()=>{
        axios.get(url)
        .then((response)=>{
            const obj = response.data;
            setdata(obj);
        })
    }
    return (
            <tbody> 
                {data.map((res)=>(
                <tr key={res.id}>
                    <th scope="row">{res.rollno}</th>
                    <td>{res.name}</td>
                    <td>{res.dob}</td>
                    <td>{res.cls}</td>
                    <td>{res.division}</td>
                    <td>{res.gender}</td>
                </tr>
                ))}
                
            </tbody>
    )
}
