import React,{useState} from 'react'
import axios from 'axios';
import '../css/Table.css'
import { useHistory } from 'react-router-dom'

export default function Table() {

    // As this is a assignment project not using any npm modules for validating form

    // Assigining dynamically values for class field in the form
    const vals=[{id:1,value:'I'},{id:2,value:'II'},{id:3,value:'III'},{id:4,value:'IV'},{id:5,value:'V'},
    {id:6,value:'VI'},{id:7,value:'VII'},{id:8,value:'VIII'},{id:9,value:'IX'},{id:10,value:'X'}];


    //Validating Student name
    const regex= new RegExp('^[a-zA-Z ]{2,30}$') 
    const [name,setName]=useState('')
    const [nameerr,setNameErr]=useState(false)
    const validatename = ()=>{
        if(!regex.test(name)){
            setNameErr(true)
            return true    
        }else{
            return false
        }
    }

    //getting date class division gender data
    const [date,Setdate]=useState('')
    const [cls,setCls]=useState('')
    const [division,setDivison]=useState('')
    const [gender,setGender]=useState('')


    // Redirection to next page
    let history=useHistory()
    const redirect=()=>{
        const res=validatename()
        if(res===false && date!=='' && division!=='' && cls!=='' && gender!==''){

            const refid = Date.now()%1000; //For genenrating a unique id
            const param= 'id='+refid;

            // used axios to send data to api
            axios.post('https://shijus-studentapi.herokuapp.com/add',{
                 id:refid,
                 name:name,
                 dob:date,
                 cls:cls,
                 division:division,
                 gender:gender
            }).then((res)=>{
                history.push({pathname:'/details',search:param})
            }).catch((err)=>{
                console.log("Error");
            })
        }
    }
    

    return (
        <div>
            
            
            <form  className='Tablemain'>
                <div className="mb-3">
                    <label htmlFor="exampleFormControlInput1" className="form-label">Student Name</label>
                    <input type="text" className="form-control" onChange={(e)=>{setName(e.target.value)}} required id="exampleFormControlInput1" placeholder="Eg.Steve Jobs"/>
                    {nameerr && <label htmlFor="exampleFormControlInput1" style={{color:'red'}} className="form-label">Enter a valid Name</label>}
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleFormControlInput1" className="form-label" >Date of Birth</label>
                    <input type="date" className="form-control" onChange={(e)=>{Setdate(e.target.value)}} required id="exampleFormControlInput1" placeholder="Eg.12/8/2021"/>
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleFormControlInput1" className="form-label">Class</label>
                    <select className="form-select" onChange={(e)=>{setCls(e.target.value)}} aria-label="Default select example" required>
                        <option >Select</option>
                        {vals.map((data)=>(
                            <option key={data.id} value={data.value}>{data.value}</option>

                        ))}
                    </select>
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleFormControlInput1" className="form-label">Division</label>
                    <select className="form-select" onChange={(e)=>{setDivison(e.target.value)}} required aria-label="Default select example">
                        <option>Select</option>
                        <option value="A">A</option>
                        <option value="B">B</option>
                        <option value="C">C</option>
                    </select>
                </div>
                <label htmlFor="exampleFormControlInput1" className="form-label">Gender</label>
                <div className="form-check" >
                    <input className="form-check-input" value='male' onChange={(e)=>{setGender(e.target.value)}}  type="radio" name="flexRadioDefault" required id="flexRadioDefault1"/>
                    <label className="form-check-label" htmlFor="flexRadioDefault1">
                        Male
                    </label>
                </div>
                <div className="form-check">
                    <input className="form-check-input" value='female' onChange={(e)=>{setGender(e.target.value)}}  type="radio" name="flexRadioDefault" id="flexRadioDefault2"/>
                    <label className="form-check-label" htmlFor="flexRadioDefault2">
                        Female
                    </label>
                </div>

            </form>
                <button className="btn btn-primary"  onClick={redirect} style={{marginLeft:'70%'}}>Add</button>
             
        </div>
    )
}
