import React from 'react'
import StudentTable from '../studentTable'
import Details from '../details'
import '../../css/Detailpage.css'

export default function DetailPage() {
    return (
        <div>
            <div className="row">
                <div className="col-12 col-lg-4 col-md-4 col-sm-4 col-xs-4 first">
                    <Details/>
                </div>

                <div className="col-12 col-lg-1 col-md-2 col-sm-2 col-xs-2 third">
                        {/* for just spacing */}
                </div>

                <div className="col-12 col-lg-7 col-md-6 col-sm-6 col-xs-6 Second" >
                    <StudentTable/>
                </div>
            </div>
        </div>
    )
}
